h1 {
  margin: auto;
  padding: 0;
}

.popup_field {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup_in_field {
  position: relative;
  width: 600px;
  height: 400px;
  top: 30%;
  margin: auto;
  background: #dcefff;
  border-bottom: solid 6px #3f87ce;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.25);
}

.editarea3 {
  left:150px;
  position:relative;
  margin: auto;
}

.buttonarea{
  display:flex;
  justify-content:space-around;
  padding:20px 70px;
}

.title {
  font-size: 1.5em;
  background: #5fb3f5;
  padding: 7px;
  text-align: center;
  color: #000;
  font-weight: bold;
  letter-spacing: 0.05em;
}

p {
  font-size: 1.1em;
  padding: 20px 10px;
  text-align: center;
}

.button_style_2 {
  width: 150px;
  height: 50px;
  background: #ffffff;
  border-radius: 3px;
  position: relative;
  margin: auto;
  max-width: 300px;
  padding: 10px 50px;
  color: #313131;
  transition: 0.5s ease-in-out;
  font-weight: 600;
}

.button_style_2:hover {
  background: #696969;
  color: #ffffff;
}